import React, { useState, useEffect } from 'react';
import { putNameUser } from '../../services/trybeerUserAPI';
import TopMenu from '../../components/TopMenu/Index';
import './styles.css';

const dealWithChange = (setName, setChange, value) => {
  setName(value);
  setChange(false);
};

const nameInput = (name, setName, setChange, focus, setFocus) => (
  <div className={ `${focus ? 'focus' : 'txtb'}` }>
    <label htmlFor="name">
      Name:
      <input
        type="text"
        data-testid="profile-name-input"
        id="name"
        onChange={ (event) => dealWithChange(setName, setChange, event.target.value) }
        value={ name }
        placeholder="nome"
        className="profile-name-input"
        onFocus={ () => setFocus(true) }
        onBlur={ () => setFocus(false) }
      />
      <span />
    </label>
  </div>
);

const emailInput = (email) => (
  <div>
    <label htmlFor="email">
      Email:
      <input
        type="email"
        data-testid="profile-email-input"
        id="email"
        value={ email }
        placeholder="email"
        className="profile-email-input"
        readOnly="readonly"
        disabled
      />
    </label>
  </div>
);

const saveButton = (clickToSave, isDisabled) => (
  <div>
    <button
      type="button"
      className="profile-save-btn"
      data-testid="profile-save-btn"
      onClick={ () => clickToSave() }
      disabled={ isDisabled() }
    >
      Salvar
    </button>
  </div>
);

function UserProfile() {
  const [name, setName] = useState('');
  const [email, setEMail] = useState('');
  const [token, setToken] = useState('');
  const [focusName, setFocusName] = useState(false);
  const [message, setMessage] = useState('');
  const [change, setChange] = useState(true);

  useEffect(() => {
    const actualUser = JSON.parse(localStorage.getItem('user'));
    if (!actualUser) return window.location.assign('http://localhost:3000/login');
    setName(actualUser.data.name);
    setEMail(actualUser.data.email);
    setToken(actualUser.data.token);
  }, []);

  const clickToSave = async () => {
    await putNameUser(name, email, token);
    setChange(!change);
    setMessage('Atualiza√ß√£o conclu√≠da com sucesso');
    setTimeout(() => setMessage(''), 6000);
  };

  const isDisabled = () => {
    if (name !== '' && name.length > 11 && !change) {
      return false;
    }
    return true;
  };

  return (
    <div>
      {TopMenu('Meu perfil')}
      <div className="profile-container">
        <p>{message}</p>
        {nameInput(name, setName, setChange, focusName, setFocusName)}
        {emailInput(email)}
        {saveButton(clickToSave, isDisabled)}
      </div>
    </div>
  );
}

export default UserProfile;
